import React from 'react';
import { Button, Checkbox, Form } from 'semantic-ui-react';
import { useState } from 'react';
import axios from 'axios';
import { useHistory } from "react-router";

function Create(){ 
    const [Name,setName]=useState("");
    const [Password,setPassword]=useState("");
    const [Phone,setPhone]=useState("");
    const [Age,setAge]=useState("");
    const [City,setCity]=useState("");
    const [checkbox, setCheckbox] = useState(false);
    let history=useHistory();
    let history2=useHistory();
    const name=()=>{document.getElementById('name')}
    // var regex=/^[a-zA-Z]([0-9a-zA-Z]){0,15}$/;
    // if(regex.test(Name))
    // {
        
    //     console.log("Matched");
    //     const removing=()=>{name.classList.remove('is-invalid');}
    //     removing();
    // }
    // else
    // {
    //     console.log("Not Matched");
    //     const add=()=>{name.classList.add('is-invalid');}
    //     add();
    // }
    const postData=()=>
    {
        
        axios.post('https://613c7348270b96001798b115.mockapi.io/data',{
            Name,
            Password,
            Phone,
            Age,
            City,
            checkbox
        }).then(()=>{
            history.push('/user-log');
        })

    }
    const logIn=()=>
    {
        history2.push('/user-log');
    }

    return(
        <div>
            <Form className="create-form">
                <Form.Field>
                    <label>Name</label>
                    <input type="text" name="username" placeholder='Name' classNames="name" onChange={(e)=>{setName(e.target.value)}} required/>
                    <small id="namevalid" className="form-text text-muted invalid-feedback">Your User Name must be 2-15 and should not start with a number</small>
                </Form.Field>
                <Form.Field>
                    <label>Password</label>
                    <input type="password" placeholder='Password' onChange={(e)=>{setPassword(e.target.value)}} required/>
                </Form.Field>
                <Form.Field>
                    <label>Phone</label>
                    <input type="phone" placeholder='Phone' onChange={(e)=>{setPhone(e.target.value)}} required/>
                </Form.Field>
                <Form.Field>
                    <label>Age</label>
                    <input type="number" placeholder='Age' onChange={(e)=>{setAge(e.target.value)}} required/>
                </Form.Field>
                <Form.Field>
                    <label>City</label>
                    <input type="text" placeholder='City' onChange={(e)=>{setCity(e.target.value)}} required/>
                </Form.Field>
                <Form.Field>
                    <Checkbox label='I agree entered datails is true' onChange={(e) => setCheckbox(!checkbox)}/>
                </Form.Field>
                <Button type='submit' onClick={postData}>Submit</Button>
            </Form>
            <div>
                <b className="ihave">I already have a account</b><br/>
                <button className="btn btn-info btn-lg"  onClick={logIn}>Click Here</button> 
 
            
            </div>
            
        </div>
        )
}

export default Create;